import React from "react";
import "../css/Product.css"
export default  function ({planName}) {
    //return Icon according to the planName
    const icons = {
        Basic: "data:image/svg+xml;base64,",
        Standard: "data:image/svg+xml;base64,",
        Ultimate: "data:image/svg+xml;base64,"
    };
    return (  <img src={icons[planName] ? icons[planName] : icons.Basic} alt={planName}/>)
};